.menu-container {
    width: 200px;
    @include colorBox;
    background: $primary-light;
    border: none;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0.5em 1.5em;
    margin-left: 1em;
    @include md {
        display: none;
    }
}

#mobile-menu {
    display: flex;
    width: 250px;
    z-index: 2;
    @include colorBox;
    background: $salmon;
    border: none;
    position: absolute;
    right: 0;
    top: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5em 0;
    & .menu-container {
        display: flex;
        background: $salmon;
        & a:hover {
            color: $background;
        }
    }
}

.menuItem,
.logout-button {
    font-size: clamp(0.5rem, 5vh, 1rem);
    margin-bottom: clamp(0.5rem, 5vh, 1.5rem);
    font-weight: bold;
    & a:hover {
        color: $primary-dark;
    }
}

.logout-button {
    cursor: pointer;
}
